<template>
<div class="list">
  <div class="list-top">
      <div class="list-top-style1">
        <div class="list-time"> 
          <span>时间</span>    
          <el-date-picker
            v-model="startAt"
            size="mini"
            align="center"
            type="date" 
            placeholder="开始时间"
            :picker-options="pickerOptions1">
          </el-date-picker>
          <el-date-picker  
            v-model="endAt" 
            size="mini"
            align="center"
            type="date"
            placeholder="结束时间"
            :picker-options="pickerOptions2">
          </el-date-picker>
        </div>
        <div class="list-title">
          <span>标题</span>
          <el-input v-model="title" size="mini" placeholder="请输入内容"></el-input>
        </div>
        <div class="list-author">
          <span>创建者</span>
          <el-input v-model="author" size="mini" placeholder="请输入内容"></el-input>
        </div>
    </div>
    <div class="list-top-style2">
        <div>
          <span>状态</span>
          <el-select v-model="status">
            <el-option v-for="send in statusName" 
            :label="send.name" 
            :value="send.id"
            align="center"
            >
            </el-option>
          </el-select>
        </div>
        <div>
          <span>类型</span>
          <el-select v-model="type">
            <el-option v-for="send in typeName" 
            :label="send.name" 
            :value="send.id"
            align="center"
            >
            </el-option>
            </el-select>
            <el-select v-model="industry" v-if="type === 3">
            <el-option v-for="send in industries" 
            :label="send.industryname" 
            :value="send.id"
            align="center"
            >
            </el-option>
            </el-select>
        </div>
        
    </div>
    <div class="list-btn">
          <button class="btn btn-success" @click="search">搜索</button>
          <button class="btn btn-warning" @click="clear">清空</button>
        </div>
  </div>
  <div class="list-bottom">
    <div class="list-bottom-title">
      <span>Article管理</span>  
      <button class="btn btn-success" @click="goAdd()">+新增</button>
    </div>
    <div class="list-bottom-show">
      <ul class="info-style1">
        <li>序号</li>
        <li>标题</li>
        <li>图片</li>
        <li>类型</li>
        <li>创建者</li>
        <li>创建时间</li>
        <li>修改时间</li>
        <li>状态</li>
        <li>操作</li>
      </ul>
      <!-- item是数组本身，index是数组的下标，v-for可以带三个参数 -->
      <ul class="info-style2" v-for="(item,index) in listDdata">
        <li>{{index+1}}</li>
        <li>{{item.title}}</li>
        <li><img :src="item.img"/></li>
        <li>{{item.type | changeType }}</li>
        <li>{{item.author}}</li>
        <li>
            {{item.createAt | formatDate }}
        </li>
        <li>
          {{item.updateAt | formatDate }}
        </li>
        <li>{{item.status | changeStatus }}</li>
       <li> 
          <button class="btn btn-warning" @click="changeUpDown(index)">{{item.status | changeStatus2}}</button>
          <button class="btn btn-primary" @click="goAddId(item.id)">编辑</button>
          <button class="btn btn-danger" @click="deleteList(index)">删除</button>
       </li>
      </ul>
    </div>
    
  <div>  
     
  </div>
  <div class="list-page">
            <div>
              <el-pagination 
                @current-change="handleCurrentChange" 
                :current-page="pageNum" 
                :page-size="pageSize"
                layout="total,prev,pager,next"
                :total="totalPage">
              </el-pagination>
            </div>
            <span>跳转到</span>
            <input input v-model="goNum" size='1'/>
            <span>页</span>
            <span>每页显示</span>
            <input v-model="goSize" size='1'  />
            <span>行</span>
            <button @click="goPage()" class="btn btn-success">确定</button>
    </div>
  </div>


</div>
</template>

<script>
export default {
  name: "List",
  data() {
    let that = this;
    return {
      pageSize: 10,
      pageNum: 1,
      goSize: 10,
      goNum: undefined,
      size: "",
      totalPage: undefined,
      type: "",
      status: "",
      author: "",
      title: "",
      listDdata: "",
      statusName: [
        { name: "全部", id: "" },
        { name: "草稿", id: 1 },
        { name: "上线", id: 2 }
      ],
      typeName: [
        { name: "全部", id: "" },
        { name: "首页banner", id: 0 },
        { name: "找职位banner", id: 1 },
        { name: "找精英banner", id: 2 },
        { name: "行业大图", id: 3 }
      ],
      industry: "",
      industries: [
        { id: "", industryname: "请选择" },
        { id: 0, industryname: "移动互联网" },
        { id: 1, industryname: "电子商务" },
        { id: 2, industryname: "企业服务" },
        { id: 3, industryname: "O2O" },
        { id: 4, industryname: "游戏" }
      ],
      startAt: null,
      endAt: null,
      //插件禁用时间设置
      pickerOptions1: {
        disabledDate(time) {
          if (that.endAt != null) {
            return time.getTime() > that.endAt;
          }
        }
      },
      pickerOptions2: {
        disabledDate: time => {
          return time.getTime() < that.startAt;
        }
      }
    };
  },

  // 过滤器
  filters: {
    changeStatus: function(value) {
      //注意这里不能只用一个等号，原因是value是字符串，不能直接判断数值
      if (value == 1) {
        return "草稿";
      } else {
        return "上线";
      }
    },
    changeStatus2: function(value) {
      //注意这里不能只用一个等号，原因是value是字符串，不能直接判断数值
      if (value == 1) {
        return "上线";
      } else {
        return "下线";
      }
    },
    changeType: function(value) {
      switch (value) {
        case 0:
          return "首页banner";
          break;
        case 1:
          return "找职位banner";
          break;
        case 2:
          return "找精英banner";
          break;
        case 3:
          return "行业大图";
          break;
      }
    },
    //时间戳转换
    formatDate: function(value) {
      let date = new Date(value);
      let y = date.getFullYear();
      let MM = date.getMonth() + 1;
      MM = MM < 10 ? "0" + MM : MM;
      let d = date.getDate();
      d = d < 10 ? "0" + d : d;
      let h = date.getHours();
      h = h < 10 ? "0" + h : h;
      let m = date.getMinutes();
      m = m < 10 ? "0" + m : m;
      let s = date.getSeconds();
      s = s < 10 ? "0" + s : s;
      return y + "-" + MM + "-" + d + " " + h + ":" + m + ":" + s;
    }
  },

  // 在页面加载时，先调用搜索功能
  created() {
    this.search();
  },
  methods: {
    // 搜索
    search: function() {
      let time1, time2;
      if (this.startAt != null) {
        time1 = this.startAt.valueOf();
      }
      if (this.endAt != null) {
        time2 = this.endAt.valueOf();
      }
      if ((time1 = time2)) {
        time2 = time2 + 86399000;
      }
      this.axios({
        method: "GET",
        url: "/carrots-admin-ajax/a/article/search",
        params: {
          type: this.type,
          status: this.status,
          author: this.author,
          title: this.title,
          startAt: time1,
          endAt: time2,
          size: this.pageSize,
          page: this.pageNum,
          
        }
      }).then(res => {
        // console.log(res);
        this.listDdata = res.data.data.articleList;
        this.totalPage = res.data.data.total;
      });
    },
    // 清空搜索
    clear: function() {
      this.title = "";
      this.type = "";
      this.status = "";
      this.author = "";
      this.title = "";
      this.startAt = null;
      this.endAt = null;
      this.pageSize = 10;
      this.pageNum = null;
      this.axios({
        method: "GET",
        url: "/carrots-admin-ajax/a/article/search",
        params: {
          type: null,
          status: null,
          author: null,
          title: null,
          startAt: null,
          endAt: null,
          size: null,
          page: null
        }
      }).then(res => {
        // console.log(res);
        this.listDdata = res.data.data.articleList;
        this.totalPage = res.data.data.total;
      });
    },
    // 添加
    goAdd: function() {
      this.$router.push({ path: "Add" });
    },
    //上下线改变
    changeUpDown: function(index) {
      let that = this;
      let id = that.listDdata[index].id;
      let status = that.listDdata[index].status;
      // console.log(id);
      // console.log(status);
      if (status === 1) {
        that
          .$confirm("确定要上线？", "提示", {
            confirmButtonText: "确定",
            cancelButtonText: "取消",
            type: "warning",
            center: true
          })
          .then(() => {
            that.$message({
              type: "success",
              message: "上线成功!"
            });
            that
              .axios({
                method: "put",
                url: "/carrots-admin-ajax/a/u/article/status",
                params: {
                  id: id,
                  status: 2
                }
              })
              .then(res => {
                that.search();
              });
          })
          .catch(() => {
            that.$message({
              type: "info",
              message: "已取消操作"
            });
          });
      } else {
        that
          .$confirm("确定要转为草稿？", "提示", {
            confirmButtonText: "确定",
            cancelButtonText: "取消",
            type: "warning",
            center: true
          })
          .then(() => {
            that.$message({
              type: "success",
              message: "已经转为草稿"
            });
            that
              .axios({
                method: "put",
                url: "/carrots-admin-ajax/a/u/article/status",
                params: {
                  id: id,
                  status: 1
                }
              })
              .then(res => {
                //调用search重新获取新的列表
                that.search();
              });
          })
          .catch(() => {
            that.$message({
              type: "info",
              message: "已取消操作"
            });
          });
      }
    },

    goAddId: function(id) {
      this.$router.push({
        name: "Add",
        params: {
          id: id
        }
      });
    },

    //删除
    deleteList: function(index) {
      let that = this;
      let id = that.listDdata[index].id;
      // console.log(id);
      that
        .$confirm("确定要删除？", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning",
          center: true
        })
        .then(() => {
          that.$message({
            type: "success",
            message: "删除成功！"
          });
          that
            .axios({
              method: "delete",
              url: "/carrots-admin-ajax/a/u/article/" + id
            })
            .then(res => {
              //调用search重新获取新的列表
              that.search();
            });
        })
        .catch(() => {
          that.$message({
            type: "info",
            message: "已取消操作"
          });
        });
    },
    // handleSizeChange(val) {
    //   this.pageSize = val;
    //   this.search();
    // },
    //上一页/下一页
    handleCurrentChange(val) {
      this.pageNum = val;
      this.pageSize = Number(this.goSize);
      this.search();
    },
    //跳转到第几行第几页
    goPage: function() {
      let that = this;
      let pagecheck = /(^[1-9]\d*$)/.test(that.goNum);
      // console.log(pagecheck);
      if (that.goNum != undefined && that.goNum != "" && pagecheck === false) {
        that.$alert("请输入大于0的整数页码", "提示", {
          confirmButtonText: "确定",
          type: "warning",
          center: true
        });
      } else if (!/(^[1-9]\d*$)/.test(that.goSize)) {
        that.$alert("请输入大于0的整数行数", "提示", {
          confirmButtonText: "确定",
          type: "warning",
          center: true
        });
        //输入的跳转页面大于实际页面的处理
      } else if (that.goNum > Math.ceil(that.totalPage / that.goSize)) {
        that.goNum = "";
        that.pageNum = Number(Math.ceil(that.totalPage / that.goSize));
        that.pageSize = Number(that.goSize);
        that.search();
      } else {
        //逻辑顺序不能乱
        that.pageSize = Number(that.goSize);
        //转换数据类型为number，防止一个vue警告的BUG
        that.pageNum = Number(that.goNum);
        that.goNum = "";
        that.search();
      }
    }
  }
};
</script>



<style scoped lang="scss">
.list {
  padding: 1rem;
}

.list-top {
  padding: 1rem;
  border: 1px solid #ccc;
  overflow: hidden;
}

.list-top-style1 {
  span {
    margin-right: 1rem;
  }
  div {
    display: inline-block;
    padding: 0.5rem;
    width: 33%;
    text-align: center;
    div {
      width: 42%;
    }
    span {
      vertical-align: middle;
    }
  }
}
.list-top-style2 {
  span {
    margin-right: 1rem;
  }
  div{
    display: inline-block;
    padding:1rem;
  }
}

.list-btn {
  float:right;
  button {
    margin-right: 1rem;
  }
}

.list-bottom {
  margin-top: 1rem;
  border: 1px solid #ccc;
}

.list-bottom-title {
  overflow: hidden;
  padding: 1rem;
  border-bottom: 1px solid #ccc;
  span {
    font-size: 2rem;
    font-weight: bold;
  }
  button {
    float: right;
    vertical-align: middle;
  }
}

.info-style1 {
  list-style-type: none;
  margin: 0 auto;
  border-bottom: 1px solid #ccc;
  li {
    display: inline-block;
    width: 11%;
    margin: 1rem auto;
    text-align: center;
    vertical-align: middle;
  }
}
.info-style2 {
  list-style-type: none;
  margin: 0 auto;
  border-bottom: 1px solid #ccc;
  li {
    display: inline-block;
    width: 11%;
    margin: 1rem auto;
    text-align: center;
    vertical-align: middle;
  }
  img {
    width: 6rem;
    max-height: 5rem;
  }
  button {
    display: block;
    margin: 0.5rem auto;
  }
}

.list-page {
  float: right;
  padding: 1rem;
  div {
    display: inline-block;
    vertical-align: middle;
  }
  span {
    padding: 0 1rem;
  }
}
</style>
