<template>
<div>
  <ul class="nav-one">
  <p class="nav-title">萝卜多后台管理系统</p>
  <li v-for="(item1,index1) in menuList">
        <!-- 注意这个click放置的位置 -->
      <div @click="showOne(item1,index1)" >{{item1.name}}</div> 
      <ul class="nav-two"  v-show="item1.show">
        <li @click="changeTwo(index1,index2)"
          :class="{'two-back':item2.style === true}"
          v-for="(item2,index2) in item1.title">
          <!-- router-link在编译时会转化为a标签，所以添加样式要给a标签加 -->
          <router-link :to="{path:item2.path}" >{{item2.name}}</router-link>
        </li>
      </ul>
  </li>
</ul> 
<header>
  <div class="nav-right">
    <span>用户名</span>
    <span>admin</span>
    <button class="btn btn-warning" type="button" @click="signout()">退出</button>
  </div>
</header>

<router-view class="show-me">

</router-view>
</div>

</template>

<script>
export default {
  //输出名称
  name: "Back",
  data() {
    return {
      ind: false,
      menuList: [
        {
          name: "信息管理",
          show: false,
          title: [
            {
              name: "公司列表",
              path: "",
              style: false
            },
            {
              name: "职业列表",
              path: "",
              style: false
            }
          ]
        },
        {
          name: "Article管理",
          show: false,
          title: [
            {
              name: "Article列表",
              path: "/Back/List",
              style: false
            },
            {
              name: "文章管理",
              path: "",
              style: false
            }
          ]
        },
        {
          name: "用户管理",
          show: false,
          title: [
            {
              name: "用户列表",
              path: "",
              style: false
            }
          ]
        },
        {
          name: "内容管理",
          show: false,
          title: [
            {
              name: "视频管理",
              path: "",
              style: false
            }
          ]
        }
      ]
    };
  },
  //在页面加载之前执行下列操作
  created() {
    //从methods中调用事件
    this.checkStatus();
  },

  methods: {
    checkStatus() {
      //获取菜单保存
      let login = sessionStorage.getItem("login");
      // console.log(login);
      if (login == undefined) {
        alert("未登录！");
        this.$router.push({ path: "/" });
      }
      let oneList = sessionStorage.getItem("oneList");
      let twoList = sessionStorage.getItem("twoList");
      if (oneList) {
        //如果之前有保存状态
        this.menuList[oneList].show = true;
        // console.log(xiabiao);
        if (twoList) {
          this.menuList[oneList].title[twoList].style = true;
        }
      }
    },
    // 点击展开折叠菜单事件
    showOne: function(item1, index1) {
      this.menuList.forEach(i => {
        // 判断如果数据中的menuList[i]的show属性不等于当前数据的show属性那么menuList[i]等于false
        if (i.show !== this.menuList[index1].show) {
          i.show = false;
        }
      });
      item1.show = !item1.show; //点击按钮，反转对应列表show属性的布尔属性
      if (item1.show === true) {
        //要用三个等号。状态保存
        sessionStorage.setItem("oneList", index1);
      } else {
        sessionStorage.removeItem("oneList");
      }
    },
    changeTwo: function(index1, index2) {
      this.menuList.forEach(function(value, index) {
        if (index != index1) {
          value.title.forEach(function(value) {
            value.style = false;
          });
        } else {
          value.title.forEach(function(value, index) {
            if (index != index2) {
              value.style = false;
            }
          });
        }
      });
      this.menuList[index1].title[index2].style = !this.menuList[index1].title[
        index2
      ].style;
      if (this.menuList[index1].title[index2].style === true) {
        sessionStorage.setItem("twoList", index2);
      } else {
        sessionStorage.removeItem("twoList");
      }
    },
    signout: function() {
      sessionStorage.removeItem("login");
      this.$router.push({ path: "/" });
    }
  }
};
</script>

<!-- 添加scoped让css样式只能对当前组件起作用 -->
 <style scoped lang="scss">
* {
  margin: 0;
  padding: 0;
}

header {
  overflow: hidden;
  background-color: #ccc;
  height: 4rem;
}

.nav-right {
  float: right;
  padding: 0.5rem;
  color: rgb(255, 115, 0);
  span {
    padding: 0.5rem;
  }

  span:hover {
    background-color: #464646;
  }

  button {
    padding: 0.5rem 1rem;
  }
}

.nav-title {
  height: 4rem;
  line-height: 4rem;
  border-bottom: 1px solid white;
  text-align: center;
}

.nav-one {
  position: fixed;
  width: 250px;
  top: 0;
  left: 0;
  bottom: 0;
  background: rgb(35, 42, 58);
  color: white;
  text-align: center;
  font-size: 2rem;
  list-style: none;

  li {
    cursor: pointer;

    border-bottom: 1px solid #464646;
    div {
      height: 4rem;
      line-height: 4rem;
      text-align: center;
      background: black;
    }
    div:hover {
      background: rgb(50, 66, 50);
    }
  }
}

.nav-two {
  a {
    display: block;
    height: 4rem;
    color: white;
    font-size: 1.5rem;
    line-height: 4rem;
    text-align: center;
    text-decoration: none;
    border-bottom: 1px solid #ccc;
  }
  a:hover {
    background: rgb(70, 102, 99);
  }
}

.two-back {
  background: rgb(70, 102, 99);
}

.show-me {
  margin-left: 250px;
}
</style>
