<template>
<!-- 单独页面背景的设置方法，fixed -->
      <div class="backimg">
        <div class="loginWrap">
            <h1>后台登陆</h1>
            <div class="inputWrap" >
              <img src="../assets/JS5user.png" width="20" />
              <input placeholder="请输入用户名" v-model="user" type="text"/>
              
            </div>
            <div class="inputWrap">
              <img src="../assets/JS5password.png" width="20" />
              <input placeholder="请输入密码" v-model="pwd" type="password"/>  
            </div>
            <button class="btn btn-primary" @click="goList" >登陆</button>
            <span class="red" v-if="warnword">{{warnword}}</span>
        </div>  
        
      </div>

</template>

<script>
export default {
  
  //输出名称
  name: "Login",
  data() {
    return {
      user: "",
      pwd: "",
      warnword: ""
    };
  },

  methods: {
    //http请求和页面跳转
    goList: function() {
      this.axios({
        method: "POST",
        url: "/carrots-admin-ajax/a/login",
        params: {
          name: this.user,
          pwd: this.pwd
        }
      })
        .then(res => {
          if (res.data.code === 0) {
            console.log(res);
            //页面跳转
            sessionStorage.setItem("login", 1);            
            this.$router.push({ path: "/Back" });       
          } else {
            //获取服务器返回的错误信息
            this.warnword = res.data.message;
          }
        })
        .catch(function(error) {
          console.log(error);
        });
    }
  }
};
</script>
<!-- 添加scoped让css样式只能对当前组件起作用,删掉了-->
<!-- 图片的导入方法有很多，这里采用了最简单的，直接引入文件路径-->
<style scoped lang="scss">
// 用这个方法设置单独页面的背景
.backimg {
  position: fixed;
  height: 100%;
  width: 100%;
  background-repeat: no-repeat;
  background-image: url(../assets/413471.png);
}

.inputWrap {
  display: block;
  margin: 2rem auto;
  img {
    margin-right: 0.5rem;
    vertical-align: middle;
  }
  input {
    vertical-align: middle;
  }
}

.loginWrap {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  height: 25rem;
  width: 30rem;
  text-align: center;
  border: 1px solid #aaa;
  border-radius: 5px;
  background-color: sandybrown;
}

.loginBtn {
  padding: 0.2rem 2rem;
  font-size: 1.5rem;
  color: white;
  border: none;
  border-radius: 5px;
  background-color: steelblue;
  box-shadow: none;
  outline: none;
}

.red {
  display: block;
  color: red;
}
</style>
