<template>
  <div>
    <div class="noleft"></div>
   <div class="add">
      <div class="add-title">
       {{addTitle}}
      </div>
      <div>
        <label>标题名称<br/>必填项</label>
        <el-input class="add-input" v-model="title" placeholder="请输入内容"></el-input>
      </div>
      <div>
        <label>类&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;型<br/>必填项</label>
        <!-- 绑定type之后，下面的value会和type绑定，当send.id为""时，对应name为请选择 -->
        <el-select v-model="type">
            <el-option v-for="send in typeName" 
            :label="send.name" 
            :value="send.id"
            >
            </el-option>
            </el-select>
            <el-select v-model="industry" v-if="type === 3">
            <el-option v-for="send in industries" 
            :label="send.name" 
            :value="send.id"
            align="center"
            >
            </el-option>
            </el-select>
      </div>
      <div>
        <label>跳转链接<br/>必填项</label>
         <el-input class="add-input" v-model="link" placeholder="请输入内容"></el-input>
      </div>
      <div>
        <label class="bugFix">内&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;容</label>
      </div>
      <div id="editorElem" :content="text"></div>
      <div>
        <label >选择图片</label>
        <!-- 一个label和input的绑定  -->
        <label for="file" class="btn btn-primary img-choose">点击上传</label>
        <input @change="fileImage" 
        type="file" accept="image/*" 
        name="file" id="file" :value="bugFix" class="noshow"/>
      </div>
      
      <div class="img-view"><img :src="imgSend"/></div>   
      <table class="uploadbox">
          <tr>
              <th class="left-border">图片</th>
              <th>大小</th>
              <th>进度</th>
              <th>状态</th>
              <th>操作</th>
          </tr>
          <tr v-if="imgLocal">
            <!--  -->
              <td>{{imgLocal.name}}</td>
              <td>{{(imgLocal.size/1048576).toFixed(3)}}MB</td>
              <td>
                <el-progress :text-inside="true" :stroke-width="18" :percentage="proGress" color="rgba(1, 122, 183,0.7)"></el-progress>
              </td>
              <td><span>{{upInfo}}</span></td>
              <td>
                  <button @click="clear" class="btn btn-danger">删除</button>
                  <button @click="upLoadImg" class="btn btn-primary">上传</button>
              </td>
          </tr>
          
      </table>
        <div class="add-bottom-btn">
          <button class="btn btn-success" @click="goLive()">立即上线</button>
          <button class="btn btn-primary" @click="setDraft()">存为草稿</button>
          <button class="btn btn-warning" @click="goList()">取消</button>
      </div>
    </div>
  </div>
</template>

<script>
// 在这里引入富文本编辑器的JS和css代码
import E from "wangeditor";
import "wangeditor/release/wangEditor.min.css";
export default {
  name: "Add",
  data() {
    return {
      //bugfix这个数据，在每次点击删除按钮时都清空，防止一个不能重复上传同一张图片的bug
      addTitle: "",
      link: "",
      title: "",
      upInfo:"图片未上传",
      proGress: 0,
      bugFix: "",
      imgSend: "",
      imgLocal: "",
      text: "",
      textClear: "",
      textSet: "",
      type: "",
      status: "",
      imgBack: "",
      typeName: [
        { name: "请选择", id: "" },
        { name: "首页banner", id: 0 },
        { name: "找职位banner", id: 1 },
        { name: "找精英banner", id: 2 },
        { name: "行业大图", id: 3 }
      ],
      industry: "",
      industries: [
        { id: "", name: "请选择" },
        { id: 0, name: "移动互联网" },
        { id: 1, name: "电子商务" },
        { id: 2, name: "企业服务" },
        { id: 3, name: "O2O" },
        { id: 4, name: "游戏" }
      ]
    };
  },

  created() {
    let id = this.$route.params.id;
    //  console.log(id)
    if (id) {
      let that = this;
      that.addTitle = "编辑Article";
      this.axios({
        method: "GET",
        url: "/carrots-admin-ajax/a/article/" + id
      }).then(res => {
        //获取id对应的list数据
        let listInfo = res.data.data.article;
        that.link = listInfo.url;
        that.title = listInfo.title;
        that.type = listInfo.type;
        that.industry = listInfo.industry;
        that.text = listInfo.content;
        that.status = listInfo.status;
        //调用富文本编辑器的插入函数
        that.textSet(that.text);
        that.imgSend = listInfo.img;
      });
    } else {
      this.addTitle = "新增Article";
    }
  },
  methods: {
    // 一个原生获取本地图片预览的写法
    fileImage: function(e) {
      let that = this;
      let file = e.target.files[0];
      if (file.size / 1048576 > 1) {
        that.$alert("图片大小超过1MB", "提示", {
          confirmButtonText: "确定",
          center: true,
          type: "warning",
          callback: action => {
            that.bugFix = "";
            file = undefined;
          }
        });
      } else {
        that.imgLocal = file;
        let reader = new FileReader();
        // 图片转换 base64格式
        reader.readAsDataURL(file);
        reader.onloadend = function() {
          // 图片的 base64 格式, 可以直接当成 img 的 src 属性值
          //图片与imgSend数据绑定
          that.imgSend = reader.result;
        };
      }
    },
    //删除
    clear: function() {
      this.imgLocal = "";
      this.imgSend = "";
      this.bugFix = "";
      this.proGress = 0;
      this.upInfo = "图片未上传";
      //调用清除富文本编辑器内容的方法
      this.textClear();
    },
    //上传图片
    upLoadImg: function() {
      let that = this;
      let file = new FormData();
      file.append("file", that.imgLocal);
      that
        .axios({
          method: "post",
          url: "/carrots-admin-ajax/a/u/img/task",
          data: file, //划重点
          headers: { "Content-Type": "multipart/form-data" }, //划重点
          onUploadProgress: function(progressEvent) {
            var percent = parseInt((100 * progressEvent.loaded) / progressEvent.total);
            that.proGress = percent;
          }
        })
        .then(res => {
          // console.log(res);
          that.upInfo = "上传成功！";
          that.imgSend = res.data.data.url;
        })
        .catch(res => {
          // console.log("失败");
        });
    },
    //立即上线
    goLive: function() {
      let that = this;
      let id = that.$route.params.id;
      let url = "/carrots-admin-ajax/a/u/article/";
      let methods = "post";
      if (id) {
        url = url +id;
        methods = "put";
      }
      this.axios({
        method: methods,
        url: url,
        params: {
          title: that.title,
          type: that.type,
          status: 1,
          img: that.imgSend,
          //上传的content等于富文本编辑器中的内容
          content: that.text,
          url: that.link,
          industry: that.industry
        },
        headers: {
          "Content-Type": "application/x-www-form-urlencoded"
        }
      }).then(res => {
        console.log("提交成功！");
         this.$router.push({ path: "List" });
      });
    },
    setDraft: function() {
      let that = this;
      let id = that.$route.params.id;
      let url = "/carrots-admin-ajax/a/u/article/";
      let methods = "post";
      if (id) {
        url = url +id;
        methods = "put";
      }
      this.axios({
        method: methods,
        url: url,
        params: {
          title: that.title,
          type: that.type,
          status: 2,
          img: that.imgSend,
          //上传的content等于富文本编辑器中的内容
          content: that.text,
          url: that.link,
          industry: that.industry
        },
        headers: {
          "Content-Type": "application/x-www-form-urlencoded"
        }
      }).then(res => {
        console.log("提交成功！");
         this.$router.push({ path: "List" });
      });
    },
    goList:function(){
      this.imgLocal = "";
      this.imgSend = "";
      this.bugFix = "";
      this.proGress = 0;
      this.type = "";
      this.industry = "";
      this.text = "";
      this.upInfo = "";
      //调用清除富文本编辑器内容的方法
      this.textClear();
       this.$router.push({ path: "List" });
    }
  },

  mounted() {
    var editor = new E("#editorElem");
    editor.customConfig.onchange = html => {
      this.text = html;
    };
    editor.create();
    //插入富文本编辑器内容
    this.textSet = function(text) {
      editor.txt.html("<p>" + text + "</p>");
    };
    //清除富文本编辑器内容
    this.textClear = function() {
      editor.txt.clear();
    };
  }
};
</script>


<style scoped lang="scss">
* {
  margin: 0;
  padding: 0;
}
.noleft {
  margin-left: 250px;
}
.add {
  margin: 2rem;
  overflow: hidden;
  border: 1px solid #ccc;
  div {
    padding: 1rem;
  }
  label {
    margin-right: 1rem;
    font-size: 2rem;
    vertical-align: middle;
    text-align: center;
  }
  img {
    max-width: 100%;
  }
  select {
    margin-right: 1rem;
  }
}

.add-title {
  font-size: 2rem;
  font-weight: bold;
}

.add-input{
  width:50%;
  z-index: 99;
}

.noshow {
  display: none;
}

.img-choose {
  padding: 0.5rem 1rem;
}

.img-view {
  width: 100%;
  text-align: center;
  margin: 1rem auto;
  img {
    vertical-align: middle;
    max-width: 100%;
  }
}

.uploadbox {
  width: 100%;
  text-align: center;

  th {
    width: 15%;
    padding: 1rem;
    text-align: center;
    border: 1px solid #ccc;
    border-right: none;
  }

  td {
    height: auto;
    padding: 1rem;
    word-wrap: break-word;
    word-break: break-all;
    overflow: hidden;
    text-align: center;
    border: 1px solid #ccc;
    border-right: none;
  }
  button {
    padding: 0.5rem 1rem;
    margin-right: 0.5rem;
    margin-top: 0.5rem;
  }
}

.left-border {
  border-left: none !important;
}
.add-bottom-btn {
  float: right;
  padding: 1rem;
  button {
    margin-right: 1rem;
    padding: 0.5rem 1rem;
  }
}

.bugFix{
  margin-top: 5rem;
}


</style>
